<template>
  <div id="index" v-if="user.data">
    <div class="header">
      <nav class="navbar">
        <div class="navbar-brand">
          <a class="navbar-item" to="/">
            <span class="icon is-small " style="margin-right:7px">
              <i class="fa fa-rocket has-text-primary"></i>
            </span>
            <strong>Simple Builder</strong>
          </a>

          <div class="navbar-burger burger" data-target="navMenubd-example">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>

        <div id="navMenubd-example" class="navbar-menu">
          <div class="navbar-start">
            <router-link class="navbar-item" to="/" exact>Home</router-link>
            <router-link class="navbar-item" to="/websites/create">Create website</router-link>
            <router-link class="navbar-item" :to="'/websites/1/build'">first website</router-link>
          </div>

          <div class="navbar-end">
            <strong class="navbar-item">
              {{ user.welcomeMessage() }}
            </strong>

            <div class="navbar-item">
              <div class="dropdown is-right is-hoverable ">
                <div class="dropdown-trigger">
                  <button class="button is-primary is-outlined " aria-haspopup="true" aria-controls="dropdown-menu6">
                    <span>Login</span>
                    <span class="icon is-small">
                      <i class="fa fa-angle-down" aria-hidden="true"></i>
                    </span>
                  </button>
                </div>
                <div class="dropdown-menu" role="menu" style="width:300px">
                  <div class="dropdown-content ">
                    <div class="dropdown-item">
                        <div class="field">
                          <label class="label">Name</label>
                          <div class="control">
                            <input class="input" type="text" placeholder="e.g Alex Smith">
                          </div>
                        </div>

                        <div class="field">
                          <label class="label">Email</label>
                          <div class="control">
                            <input class="input" type="email" placeholder="e.g. alexsmith@gmail.com">
                          </div>
                        </div>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </nav>
    </div>

    <div class="body">
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>

    <div class="modal">
      <div class="modal-background"></div>
      <div class="modal-card">
        <header class="modal-card-head">
          <p class="modal-card-title">Login</p>
          <button class="delete" aria-label="close"></button>
        </header>
        <section class="modal-card-body">
          <div class="field">
            <label class="label">Name</label>
            <div class="control">
              <input class="input" type="text" placeholder="e.g Alex Smith">
            </div>
          </div>

          <div class="field">
            <label class="label">Email</label>
            <div class="control">
              <input class="input" type="email" placeholder="e.g. alexsmith@gmail.com">
            </div>
          </div>
        </section>
        <footer class="modal-card-foot">
          <button class="button is-success">Save changes</button>
          <button class="button">Cancel</button>
        </footer>
      </div>
    </div>

  </div>
</template>

<script>
import user from '../models/User'
import router from '../router'

window.builder = {
  iframe: {
    isReady: false,
  },

  editBlock(pageIndex, blockIndex) {
    router.push('/websites/1/build/pages/'+ pageIndex +'/blocks/'+ blockIndex);
  },

  getIframe() {
    window.iframe = document.getElementById('iframe').contentWindow.iframe;
    this.iframe.isReady = true;
  }
};

export default {
  name: 'index',

  data () {
    return {
      user: user,
    }
  },

  created () {
    user.authenticate();
  },
}
</script>


<style lang="scss"> 
  @import '../assets/sass/dark.scss';

  #index {
    padding-top: 52px;
    min-height: 100vh;
  }

  .header {
    height: 52px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1
  }


  .fade-enter-active, .fade-leave-active {
    transition: opacity 0.2s ease-out;
  }

  .fade-enter, .fade-leave-active {
    opacity: 0
  }

  .page-enter-active, .page-leave-active {
    transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  }

  .page-enter, .page-leave-to {
    opacity: 0;
    transform: translateX(-10%);
  }

  .padding {
    padding: 15px;
  }
</style>



